<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、设备编号、设备名称过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="invadecode"
                    label="设备编号"
                ></el-table-column>
                <el-table-column prop="invadename" label="设备名称">
                </el-table-column>
                <el-table-column prop="invadelng" label="经度">
                </el-table-column>
                <el-table-column prop="invadelat" label="纬度">
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-row>
                    <el-col :span="9">
                        <el-form
                            :model="editForm"
                            label-width="120px"
                            :rules="editFormRules"
                            ref="editForm"
                        >
                            <el-col :span="24">
                                <el-form-item
                                    label="监测对象分组"
                                    prop="GROUPID"
                                >
                                    <el-select
                                        v-model="editForm.GROUPID"
                                        filterable
                                        placeholder="请选择"
                                        @change="groupChange"
                                    >
                                        <el-option
                                            v-for="item in groups"
                                            :key="item.GROUPID"
                                            :label="item.groupshortname"
                                            :value="item.GROUPID"
                                        >
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="监测对象" prop="SITEID">
                                    <el-select
                                        v-model="editForm.SITEID"
                                        filterable
                                        placeholder="请选择"
                                        @change="siteChange"
                                    >
                                        <el-option
                                            v-for="item in selectSites"
                                            :key="item.SITEID"
                                            :label="item.siteshortname"
                                            :value="item.SITEID"
                                        >
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item
                                    label="设备名称"
                                    prop="invadename"
                                >
                                    <el-input
                                        v-model="editForm.invadename"
                                        auto-complete="off"
                                        maxlength="20"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item
                                    label="设备编号"
                                    prop="invadecode"
                                >
                                    <el-input
                                        v-model="editForm.invadecode"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="经度" prop="invadelng">
                                    <el-input
                                        v-model="editForm.invadelng"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="纬度" prop="invadelat">
                                    <el-input
                                        v-model="editForm.invadelat"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-form>
                    </el-col>
                    <el-col :span="15">
                        <mark-map
                            ref="myMap"
                            v-if="editFormVisible"
                            :markposition="{
                                lng: editForm.invadelng,
                                lat: editForm.invadelat,
                            }"
                            @callMap="callAddMap"
                        ></mark-map>
                    </el-col>
                </el-row>

                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-row>
                    <el-col :span="9">
                        <el-form
                            :model="addForm"
                            label-width="120px"
                            :rules="addFormRules"
                            ref="addForm"
                        >
                            <el-col :span="24">
                                <el-form-item
                                    label="监测对象分组"
                                    prop="GROUPID"
                                >
                                    <el-select
                                        v-model="addForm.GROUPID"
                                        filterable
                                        placeholder="请选择"
                                        @change="groupChange"
                                    >
                                        <el-option
                                            v-for="item in groups"
                                            :key="item.GROUPID"
                                            :label="item.groupshortname"
                                            :value="item.GROUPID"
                                        >
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="监测对象" prop="SITEID">
                                    <el-select
                                        v-model="addForm.SITEID"
                                        filterable
                                        placeholder="请选择"
                                        @change="siteChange"
                                    >
                                        <el-option
                                            v-for="item in selectSites"
                                            :key="item.SITEID"
                                            :label="item.siteshortname"
                                            :value="item.SITEID"
                                        >
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item
                                    label="设备名称"
                                    prop="invadename"
                                >
                                    <el-input
                                        v-model="addForm.invadename"
                                        auto-complete="off"
                                        maxlength="20"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item
                                    label="设备编号"
                                    prop="invadecode"
                                >
                                    <el-input
                                        v-model="addForm.invadecode"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="经度" prop="invadelng">
                                    <el-input
                                        v-model="addForm.invadelng"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="24">
                                <el-form-item label="纬度" prop="invadelat">
                                    <el-input
                                        v-model="addForm.invadelat"
                                        auto-complete="off"
                                        maxlength="50"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-form>
                    </el-col>
                    <el-col :span="15">
                        <mark-map
                            ref="myMap"
                            v-if="addFormVisible"
                            @callMap="callAddMap"
                            :markposition="{
                                lng: addForm.invadelng,
                                lat: addForm.invadelat,
                            }"
                        ></mark-map>
                    </el-col>
                </el-row>

                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getinvadepagelist,
    getinvadegroups,
    removeinvade,
    editinvade,
    addinvade,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import MarkMap from "../../components/MarkMap";
export default {
    components: { leftgroup, Toolbar, MarkMap },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                invadecode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                invadename: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                invadelng: [
                    {
                        required: true,
                        message: "请填写经度",
                        trigger: "blur",
                    },
                ],
                invadelat: [
                    {
                        required: true,
                        message: "请填写纬度",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                invadecode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                invadename: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                invadelng: [
                    {
                        required: true,
                        message: "请填写经度",
                        trigger: "blur",
                    },
                ],
                invadelat: [
                    {
                        required: true,
                        message: "请填写纬度",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getinvadepagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getinvadegroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        siteChange(val) {
            let sites = this.selectSites.filter((res) => {
                return res.SITEID == val;
            });
            if (sites && sites.length > 0) {
                this.$refs.myMap.changeLoc(sites[0].sitelng, sites[0].sitelat);
                if (this.addFormVisible) {
                    this.$set(this.addForm, "invadelng", sites[0].sitelng);
                    this.$set(this.addForm, "invadelat", sites[0].sitelat);
                } else if (this.editFormVisible) {
                    this.$set(this.editForm, "invadelng", sites[0].sitelng);
                    this.$set(this.editForm, "invadelat", sites[0].sitelat);
                }
            }
        },

        callAddMap(item) {
            if (this.addFormVisible) {
                this.$set(this.addForm, "invadelng", item.point.lng);
                this.$set(this.addForm, "invadelat", item.point.lat);
            } else if (this.editFormVisible) {
                this.$set(this.editForm, "invadelng", item.point.lng);
                this.$set(this.editForm, "invadelat", item.point.lat);
            }
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.INVADEID };
                    removeinvade(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.getPageList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editinvade(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addinvade(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.bm-view {
    padding-left: 10px;
    width: 100%;
    height: 400px;
}
</style>


